export async function generateStaticParams() {
    const books = [
        { slug: 'one-pieces' },
        { slug: 'naruto' },
        { slug: 'bleach' }
    ];

    return books.map((book) => ({
        slug: book.slug
    }))
}

export default function Page({ product }) {
    return <div>
        <h1>Bạn đang xem sách: {product.name}</h1>
    </div>
}